/** @fileoverview Various utilities and helpers
 *  @externs
 *  @module
 *  @see module:type_def.h
 *  @see module:templates/app
 */
import {render} from 'lit';
import AppTemplates from './templates/app.js';

export default {
  /** Checks whether the object is properly filled by a user
   * and shows the notification if some field is not filled
   *
   * @export @func check_transaction_data
   * @param {TransactionData} transaction_data The object to check
   * @param {Element} notification_container The notification container
   * @return {boolean} Whether the object has passed the check
   */
  check_transaction_data(transaction_data, notification_container) {
    /** @type {Array<string>} */
    let errors = [];
    if (!transaction_data.destination_address) {
      errors = errors.concat("The destination address is not filled");
    } else if (!/0x[0-9a-f]{40}/.test(transaction_data.destination_address)) {
      errors = errors.concat("The destination address has wrong format");
    }
    if (!transaction_data.amount || transaction_data.amount <= 0) {
      errors = errors.concat("The amount is not filled");
    }
    if (!transaction_data.gas_price) {
      success = false;
      errors = errors.concat("The gas price is not filled");
    }
    if (!transaction_data.gas_limit) {
      success = false;
      errors = errors.concat("The gas limit is not filled");
    }
    if (errors.length) {
      render(AppTemplates.error_notifications(errors), notification_container);
      /** @const {Element} */
      const notification_item =
        notification_container.querySelector('kor-notification-item');
      notification_item.setAttribute("visible", "true");
    }
    return !errors.length;
  },
  /** Checks whether the object is properly filled by a user
   * and shows the notification if some field is not filled
   *
   * @export @func check_schedule_data
   * @param {ScheduleData} schedule_data
   * @param {Element} notification_container The notification container
   * @return {boolean} Whether the object has passed the check
   */
  check_schedule_data(schedule_data, notification_container) {
    /** @type {Array<string>} */
    let errors = [];
    if (!schedule_data.start_date) {
      success = false;
      errors = errors.concat("The start date is not filled");
    }
    if (!schedule_data.start_time) {
      success = false;
      errors = errors.concat("The start time is not filled");
    }
    if (errors.length) {
      render(AppTemplates.error_notifications(errors), notification_container);
      /** @const {Element} */
      const notification_item =
        notification_container.querySelector('kor-notification-item');
      notification_item.setAttribute("visible", "true");
    }
    return !errors.length;
  },
  /** Converts start_time in minutes into the Clock object
   *
   * @export @func start_time_to_clock
   * @param {number} start_time The time in minutes since the day started
   * @return {Clock}
   */
  start_time_to_clock(start_time) {
    /** @type {number} */
    let hour = Math.trunc(start_time / 60);
    /** @const {number} */
    const minute = start_time % 60;
    /** @const {string} */
    let am_pm = "AM";
    if (hour >= 12) {
      am_pm = "PM";
      hour -= 12;
    }
    if (hour.toFixed(0) == "0") hour = 12;
    return {
      hour: hour,
      minute: minute,
      am_pm: am_pm
    };
  }
}
